import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuardService } from '../auth0/auth-guard.service';
import { OrgsComponent } from '../orgs/orgs.component';

const orgsRouts: Routes = [
  {
    path: 'create',
    loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./org-form/org-form.module')['OrgFormModule']);  });}),
    canActivate: [AuthGuardService],
  },
  { path: ':path', component: OrgsComponent },
  {
    path: ':path/edit',
    loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./org-form/org-form.module')['OrgFormModule']);  });}),
    canActivate: [AuthGuardService],
  },
];

@NgModule({
  imports: [RouterModule.forChild(orgsRouts)],
  exports: [RouterModule],
  declarations: [],
})
export class OrgsRoutingModule {}



